/* COLORS */
:root {
  --background: #212121;
  --background-muted: #21212140;
  --foreground: #2b2b2b;
  --foreground-muted: #2b2b2b40;
  --text: #fafafa;
  --text-muted: #fafafa40;
  --primary: #ffe066;
  --primary-muted: #ffe06640;
  --secondary: #868e96;
  --secondary-muted: #868e9640;

  --dark: #00000040;
  --darker: #00000080;
  --light: #ffffff10;
  --lighter: #ffffff20;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  /* Percentage of user's browser font-size setting */
  font-size: 62.5%;
  scroll-behavior: smooth;
  font-family: 'Outfit', serif;
  background-color: var(--background);
}

body {
  color: var(--text);
  font-size: 1.4rem;
}

.grid {
  display: grid;
  column-gap: 1rem;
  row-gap: 1rem;
}

.grid-2 {
  grid-template-columns: repeat(2, 1fr);
}

.drawer {
  width: 100%;
}

.drawer-body {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  
  margin-bottom: 1rem;
  background-color: var(--dark);
  padding: 2rem;
  border-radius: 5px;
}

.noselect {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently supported by Chrome, Edge, Opera and Firefox */
}

pre {
  border: 1px dashed var(--secondary);
  color: var(--dark-text);
  page-break-inside: avoid;
  font-family: 'Inconsolata', monospace !important;
  font-size: 1.2rem;
  line-height: 1.6;
  overflow-x: hidden;
  padding: 1rem;
  display: block;
  white-space: pre;
  width: 100%;
}

input[type='text'] {
  font-family: 'Outfit', serif;
  padding: 1rem;
  border: 1px solid transparent;
  border-radius: 5px;
  background-color: var(--darker);
  box-shadow: rgba(0, 0, 0, 0.2) 0px 3px 5px 0px inset;
  transition: all 0.3s;
  color: var(--primary);
}

input[type='text']:focus {
  outline: none;
  border: 1px solid var(--primary-muted);
}

.checkbox {
  cursor: pointer;
  display: flex;
  gap: 0.8rem;
  align-items: center;

  .icon-frame {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 1.6rem;
    height: 1.6rem;
    color: var(--primary);
    border-radius: 3px;
    background-color: var(--light);
    transition: all 0.3s;
    font-size: 1rem;
  }

  .icon-frame:hover {
    background-color: var(--lighter);
  }
}

li {
  list-style: none;
}

ul.badge-group {
  display: flex;
  flex-wrap: wrap;
  gap: 0.8rem;

  li {
    padding: 0.4rem 0.8rem;
    border-radius: 5px;
    background-color: var(--light);
  }
}

.capitalize {
  text-transform: capitalize;
}

.muted {
  opacity: 0.4;
}

.text-divider {
  margin: 0 1rem;
  opacity: 0.4;
}

.condition-icon {
  color: var(--text);
}

img.flag-sm {
  height: 1.2rem !important;
}

.btn {
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  gap: 0.4rem;

  font-size: inherit;
  font-family: inherit;

  color: var(--primary);
  text-decoration: none;

  padding-block: 0;
  border: none;
  background: none;

  border-bottom: 1px dashed currentColor;
  transition: all 0.3s;

  i {
    font-size: 80%;
  }
}

.btn:focus {
  outline: none;
}

.btn:hover {
  border-bottom: 1px dashed #00000000;
}

.btn:disabled {
  cursor: default;
  opacity: 0.2;
  border-bottom: 1px dashed #00000000;
}

.btn:disabled:hover {
  border-bottom: 1px dashed #00000000;
}

.btn-header {
  font-size: 1.2rem;
  font-weight: 100;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: var(--text);
  border-radius: 99px;
  border: none;
  background-color: var(--light);
  padding: 1rem 2rem;
}

.btn-header:hover {
  background-color: var(--lighter);
  border: none;
}

h1,
h2 {
  font-size: 1.4rem;
  font-weight: 100;
  text-transform: uppercase;
  letter-spacing: 4px;
}

h1 {
  font-size: 2rem;
  letter-spacing: 8px;

  span {
    opacity: 0.4;
  }
}

h3 {
  font-weight: 100;
  letter-spacing: 1px;
}

.main {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.container {
  width: 96rem;
  margin: 0 auto;
}

header {
  border-bottom: 1px solid var(--lighter);
  padding: 3.2rem 0;
  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    div {
      display: flex;
      align-items: center;
      gap: 1rem;
    }
  }
}

article {
  position: relative;
  display: flex;
  height: 0;
  flex-grow: 1;
  margin: 2rem;
}

section {
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.wrap {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

section#list {
  width: 32rem;
  overflow-x: hidden;
  overflow-y: scroll;
}

section#details {
  width: 100%;
  overflow-x: hidden;
  overflow-y: auto;
}

.castle-list {
  display: flex;
  flex-direction: column;
  gap: 0.4rem;
}

// SEARCH & FILTERS
.inputWithCheckbox {
  position: relative;
  .checkbox {
    position: absolute;
    right: 1rem;
    top: 50%;
    transform: translateY(-50%);
  }
}

.search {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 2rem 0;

  .filters {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    width: 100%;
    align-items: center;

    .filter-full {
      flex-grow: 1;
      // width: 32rem;
    }

    .filter-short {
      width: 8rem;
      // width: 32rem;
    }

    .filter-long {
      width: 32rem;
    }
  }

  .submit-box {
    display: flex;
    gap: 1rem;
    // align-self: flex-end;
  }
}

// REUSABLES
.country-code {
  color: var(--background);
  background-color: #ffffff40;
  padding: 0.2rem 0.4rem;
  font-size: 1.2rem;
  border-radius: 5px;
}

// CASTLE LIST
.castle-tile {
  padding: 1rem 1.6rem;
  margin-right: 1rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 0.4rem;

  cursor: pointer;

  background-color: var(--light);
  transition: all 0.3s;

  h2.secondary {
    opacity: 0.4;
  }
}

.castle-tile:hover {
  background-color: var(--lighter);
}

.details {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 1.6rem;
  padding: 0.8rem;

  .info {
    border: 1px solid var(--foreground);
    padding: 2rem;

    display: flex;
    flex-direction: column;
    align-items: start;

    gap: 1rem;

    .info-header {
      display: flex;
      gap: 1rem;
      align-items: center;
      opacity: 0.4;
      margin-bottom: 1.2rem;
    }
  }

  // Locaiton
  .location {
    display: flex;
    gap: 4rem;

    .with-crest {
      display: flex;
      align-items: center;
      gap: 0.8rem;

      img {
        height: 3.2rem;
      }
    }

    .flag-sm {
      margin-right: 0.8rem;
    }
  }

  // Condition
  // .condition {
  // }

  // Dates
  // .dates {
  // }

  // Gallery
  .gallery {
    width: 100%;

    display: flex;
    gap: 1rem;
    // grid-template-columns: repeat(5, 1fr);

    .gallery-item {
      width: 4rem;
      height: 4rem;
      border-radius: 5px;

      overflow: hidden;
      background-color: var(--background);

      display: flex;
      flex-direction: column;
      position: relative;
      cursor: pointer;

      img {
        object-fit: cover;
        object-position: center;
        vertical-align: bottom;
        width: 100%;
        height: 100%;

        transition: all 0.3s;
      }

      .gallery-item-overlay {
        opacity: 0;
        position: absolute;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        color: var(--text);
      }

      .gallery-item-overlay:hover {
        opacity: 1;
        background-color: #00000040;
      }
    }
  }

  // Footer
  .details-footer {
    display: flex;
    gap: 1rem;
    justify-content: center;
  }
}

.spotlight {
  width: 100%;

  img {
    height: 24rem;
    overflow: scroll;
    object-fit: cover;
    max-width: 100%;
  }
}

// FOOTER

footer {
  padding: 2rem;
  text-align: center;

  span {
    opacity: 0.4;
  }
}
